import React from 'react';
import type { FC } from 'react';
import type { UserInfo } from '@wix/cli-auth';
import {
  Box,
  ConfirmInput,
  useAsyncCallback,
  Spinner,
  Text,
} from '@wix/cli-ui-kit';
import { Trans, useTranslation } from '@wix/cli-i18n';
import { logout } from '@wix/cli-auth';
import { match } from 'variant';

interface Props {
  userInfo: UserInfo | null;
}

export const LogoutCommand: FC<Props> = ({ userInfo }) => {
  const { t } = useTranslation();
  const logoutAction = useAsyncCallback(logout);

  if (userInfo === null) {
    return <Trans i18nKey="logout_command.not_logged_in" />;
  }

  return (
    <>
      <Box marginBottom={1}>
        <Trans i18nKey="logout_command.logout_warning" />
      </Box>

      <ConfirmInput
        label={t('logout_command.logout_confirm')}
        initialValue={true}
        onSubmit={(confirmed) => {
          if (confirmed) {
            void logoutAction.execute();
          }
        }}
      />

      {match(logoutAction.status, {
        NotRequested: () => null,
        Error: () => null,
        Loading: () => <Spinner />,
        Success: () => <Text>{t('logout_command.logout_success')}</Text>,
      })}
    </>
  );
};
